/**
 * Interview Preparation Page JavaScript
 *
 * This file contains the JavaScript for the Interview Preparation page.
 */

// Import components
import Accordion from '../components/accordion';
import Testimonials from '../components/testimonials';
import Tabs from '../components/tabs';

/**
 * Initialize the page
 */
function initPage() {
  console.log('Interview Preparation page initialized');

  // Initialize accordion
  Accordion.init({
    triggerSelector: '.faq-question',
    contentSelector: '.faq-answer',
    closeOthers: true,
    animateHeight: true
  });

  // Initialize testimonials
  Testimonials.init({
    testimonialSelector: '.testimonial',
    dotSelector: '.testimonial-dot',
    activeClass: 'active',
    autoRotate: true,
    autoRotateInterval: 5000
  });

  // Initialize tabs
  Tabs.init({
    tabsContainerSelector: '.question-categories',
    tabButtonSelector: '.question-category-tab',
    tabPanelSelector: '.question-category-content',
    activeClass: 'active'
  });

  // Set up interview question tabs
  setupInterviewQuestionTabs();

  // Set up practice interview timer
  setupPracticeInterviewTimer();
}

/**
 * Set up interview question tabs
 */
function setupInterviewQuestionTabs() {
  // Set up question reveal functionality
  const questionItems = document.querySelectorAll('.interview-question-item');

  questionItems.forEach(item => {
    const questionHeader = item.querySelector('.question-header');
    const questionAnswer = item.querySelector('.question-answer');

    if (questionHeader && questionAnswer) {
      questionHeader.addEventListener('click', function() {
        // Toggle answer visibility
        questionAnswer.classList.toggle('visible');

        // Toggle icon
        const icon = questionHeader.querySelector('i');
        if (icon) {
          icon.classList.toggle('fa-plus');
          icon.classList.toggle('fa-minus');
        }
      });
    }
  });
}

/**
 * Set up practice interview timer
 */
function setupPracticeInterviewTimer() {
  const timerContainer = document.querySelector('.practice-interview-timer');
  const startButton = document.querySelector('.start-timer-btn');
  const resetButton = document.querySelector('.reset-timer-btn');
  const timerDisplay = document.querySelector('.timer-display');

  if (timerContainer && startButton && resetButton && timerDisplay) {
    let timerInterval;
    let seconds = 0;
    let isRunning = false;

    // Start/pause timer
    startButton.addEventListener('click', function() {
      if (isRunning) {
        // Pause timer
        clearInterval(timerInterval);
        startButton.textContent = 'Resume';
        startButton.classList.remove('pause');
        startButton.classList.add('start');
      } else {
        // Start timer
        timerInterval = setInterval(updateTimer, 1000);
        startButton.textContent = 'Pause';
        startButton.classList.remove('start');
        startButton.classList.add('pause');
      }

      isRunning = !isRunning;
    });

    // Reset timer
    resetButton.addEventListener('click', function() {
      clearInterval(timerInterval);
      seconds = 0;
      updateTimerDisplay();
      startButton.textContent = 'Start';
      startButton.classList.remove('pause');
      startButton.classList.add('start');
      isRunning = false;
    });

    /**
     * Update timer
     */
    function updateTimer() {
      seconds++;
      updateTimerDisplay();
    }

    /**
     * Update timer display
     */
    function updateTimerDisplay() {
      const minutes = Math.floor(seconds / 60);
      const remainingSeconds = seconds % 60;

      timerDisplay.textContent = `${minutes.toString().padStart(2, '0')}:${remainingSeconds.toString().padStart(2, '0')}`;
    }
  }
}

// Initialize the page when DOM is ready
document.addEventListener('DOMContentLoaded', initPage);

export default {
  initPage
};
